/* eslint-disable */

import React, {useState} from 'react';
import { Navbar, Container, Nav, Jumbotron, Button, NavDropdown } from 'react-bootstrap';
import './App.css';
import data from './data.js';
import Detail from './Detail.js';
import axios from 'axios';
import { Link, Route, Switch } from 'react-router-dom';

function App() {

  let [shoes, setShoes] = useState(data);

  let [loading, setLoading] = useState(true);

  return (
    <div className="App">
       <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Container>
        <Navbar.Brand ><Link to="/">Shoes-Shop</Link></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <Nav.Link href="#pricing">Pricing</Nav.Link>
            <NavDropdown title="Dropdown" id="collasible-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Nav>
            <Nav.Link href="#deets">More deets</Nav.Link>
            <Nav.Link eventKey={2} href="#memes">
              Dank memes
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
        </Container>
      </Navbar>
      <br />

      <Switch>
        <Route exact path="/">
          <Jumbotron className="background">
            <h1>20% Season off</h1>
            <p> 쇼핑몰 만들기</p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>
          <div className="container">
            <div className="row">
              {
                shoes.map((e, i)=>{
                  return <Card shoes={shoes[i]} i={i} key={i}/>
                  // <Card shoes={e}
                })
              }
            </div>
            <button className="btn btn-primary" onClick={()=>{
              
              {
                loading === true ? 
                  <LoadingUI/>
                 : null
              }

              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result)=>{
                setLoading(false);
                setShoes( [...shoes, ...result.data] );
              })
              .catch(()=>{
                setLoading(false);
              }); //server한테 get요청

            }}>더보기</button>
          </div>
        </Route>


        <Route path="/detail/:id">
          <Detail shoes={shoes} />
        </Route>
        {/* <Route path="/어쩌구" component={Modal}></Route> */}

        <Route path="/:id">
          <div>아무거나 적었을때 이거 보여주기</div>
        </Route>

      </Switch>
    </div>
    
  );
}

function LoadingUI(){
  return(
    <div>loading...</div>
  )
}

function Card(props){
  return(

        <div className="col-md-4">
          <img alt="" src={'http://codingapple1.github.io/shop/shoes' + (props.i+1) + '.jpg'} width="100%"/>
          <h4>{props.shoes.title}</h4>
          <p>{props.shoes.content}</p>
          <p>{props.shoes.price}원</p>
          <hr/>
        </div>

  )
}

export default App;
