import React, {useState, useEffect} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.css'

let box = styled.div`
  padding : 20px;
`;

let titlee = styled.h4`font-size : 25px`;

function Detail(props){

  let { id } = useParams();
  let history = useHistory();
  let [show, setShow] = useState(true);
  
  useEffect(()=>{
    let timer = setTimeout(()=>{ setShow(false) }, 2000);
    //return function 어쩌구(){ 실행할 코드~~=>사라질 때 실행됨(=unmount될때) }
  });

  let findproduct = props.shoes.find(function(shoe){
    return shoe.id == id
  });

  return(
    <div className="container">
      <box>
        <titlee className="red">제목</titlee>
      </box>

    {
      show === true ? 
      (<div className="my-alert">
        <p>재고가 얼마 남지 않았습니다</p>
      </div>)
       : null
    }
      

          <div className="row">
            <div className="col-md-6">
              <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{findproduct.title}</h4>
              <p>{findproduct.content}</p>
              <p>{findproduct.price}원</p>
              <button className="btn btn-danger">주문하기</button> 
              <button className="btn btn-danger" 
              onClick={()=>{
                  history.goBack();
                  // history.push('/'); 특정 경로로 이동할 때
              }}>뒤로가기</button> 
            </div>
          </div>
        </div> 
  )
} 

  export default Detail;